.App {
  text-align: center;
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

div .animation{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  /* Puts it behind other content */
  background: transparent;
  /* Makes it transparent */
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

#layer2{
  background-color: #61dafb;
}

#layer3 {
  background-color: #6361fb;
}